import userInfo from './info'
// import { deepClone } from '../utils/common'
export default {
  name: 'bookMenu',
  components: { userInfo },
  data () {
    return {
      stuMenu: [{
        name: '待审核',
        link: '/bookCourse/booking',
        icon: 'icon-dengdai2'
      }, {
        name: '预约成功',
        link: '/bookCourse/success',
        icon: 'icon-chenggong'
      }, {
        name: '预约失败',
        link: '/bookCourse/fail',
        icon: 'icon-shibai'
      }, {
        name: '预约取消',
        link: '/bookCourse/end',
        icon: 'icon-quxiao2'
      }],
      teaMenu: [{
        name: '待预约',
        link: '/bookCourse/booking',
        icon: 'icon-dengdai2'
      }, {
        name: '已预约',
        link: '/bookCourse/success',
        icon: 'icon-chenggong'
      }, {
        name: '已失效',
        link: '/bookCourse/fail',
        icon: 'icon-shibai'
      }, {
        name: '已结束',
        link: '/bookCourse/end',
        icon: 'icon-quxiao'
      }],
      menu: []
    }
  },
  computed: {
    hasInfo () {
      return this.$store.state.user.hasInfo
    },
    showText () {
      if (this.$store.state.user.userType === 'student') {
        return '我的预约'
      } else {
        return '我的课程'
      }
    },
    userType () {
      return this.$store.state.user.userType
    }
  },
  mounted () {
    this.initIcon()
  },
  watch: {
    userType () {
      this.initIcon()
    }
  },
  methods: {
    initIcon () {
      if (this.$store.state.user.userType === 'student') {
        this.menu = this.stuMenu
      } else {
        this.menu = this.teaMenu
      }
    },
    goto (url) {
      if (this.hasInfo) {
        this.$router.push({ path: url })
      } else {
        this.gotoReg()
      }
    },
    gotoAll () {
      if (this.hasInfo) {
        this.$router.push({ path: '/bookCourse/all' })
      } else {
        this.gotoReg()
      }
    },
    gotoReg () {
      this.$router.push({ name: 'register' })
    }
  }
}



