export default {
  computed: {
    hasInfo () {
      return this.$store.state.user.hasInfo
    }
  },
  methods: {
    gotoCreate () {
      if (this.hasInfo) {
        this.$store.commit('SET_RETURN_TEXT', '新建课程')
        this.$store.commit('SET_RETURN_URL', this.$route.path)
        this.$router.push({ path: '/createCourse' })
      } else {
        this.gotoReg()
      }
    },
    gotoReg () {
      this.$router.push({ name: 'register' })
    }
  }
}



